import React, {useState, useCallback} from 'react';
import {useStaticQuery, graphql} from 'gatsby';

import PropTypes from 'prop-types';
import Link from 'gatsby-link';
import _ from 'lodash';

import './navbar.scss';
import searchIcon from '../images/search.svg';

function Navbar() {
  const [filter, setFilter] = useState('');

  const _setFilter = useCallback(
    ({target: {value}}) => {
      setFilter(value);
    },
    [setFilter]
  );

  const getMarkdownPages = useCallback(data => {
    const markdownPages = data.allFile.edges;
    const pages = _.chain(markdownPages)
      .map(({node}) => node.childMarkdownRemark.frontmatter)
      .sortBy('index')
      .value();

    return pages;
  }, []);

  const getNavbarComponents = useCallback(data => {
    const components = data.allComponentMetadata.edges;
    const filteredComponents = _.chain(components)
      .filter(component => component.node.displayName !== 'IGNORE')
      .sortBy('node.displayName')
      .value();

    return filteredComponents;
  }, []);

  const renderNavbar = useCallback(
    data => {
      // const markdowns = getMarkdownPages(data);
      const components = getNavbarComponents(data);

      const filteredComponents = _.filter(components, component =>
        _.includes(_.lowerCase(component.node.displayName), _.lowerCase(filter))
      );
      return (
        <div className="navbar">
          <div className="search">
            <img src={searchIcon} />
            <input placeholder="Search..." onChange={_setFilter} />
          </div>
          <ul>
            {/* {_.map(markdowns, page => {
              return (
                <li>
                  <Link to={page.path}>{page.title}</Link>
                </li>
              );
            })} */}
            <li className="separator" />
            {_.map(filteredComponents, (component, index) => {
              return (
                <li key={index}>
                  <Link
                    key={component.node.displayName}
                    to={`/docs/${component.node.displayName}/`}
                  >
                    {component.node.displayName}
                    {/* {component.node.isPublic && <span className="public">public</span>} */}
                  </Link>
                </li>
              );
            })}
          </ul>
        </div>
      );
    },
    [filter, _setFilter, getMarkdownPages, getNavbarComponents]
  );

  const data = useStaticQuery(
    graphql`
      query LayoutQuery {
        allComponentMetadata {
          edges {
            node {
              displayName
            }
          }
        }
      }
    `
  );

  return renderNavbar(data);
}

Navbar.propTypes = {
  components: PropTypes.array
};

export default Navbar;
